<template>
  <!-- 大赛介绍 -->
  <div class="game-introduce">
    <div class="introduce">
      <div class="title">
        第18届中国日报社“21世纪杯”小学生英语演讲大会
        暨第4届少儿英语大会
      </div>
      <div
        class="content"
        :class="{'content-start': !contentPartState}"
      >{{contentPartState?introduceContent:contentPart}}</div>
      <div class="btn">
        <!-- v-show="!contentPartState" -->
        <span @click="clickRxpand">{{!contentPartState?`展开查看`:`收起查看`}}</span>
      </div>
    </div>
    <!-- 协作单位 -->
    <div class="cooperation-units">
      <div class="title-box">
        <tit-comp tit="协作单位"></tit-comp>
        <div class="arrow-box" @click="toList('units')">
          <img src="../../../assets/img/arrows.png" class="arrow">
        </div>
      </div>
      <div class="units-box" v-if="units">
        <list-x v-for="(item, index) in units" :key='index' :onItem='item'></list-x>
      </div>
    </div>
    <!-- 大赛评委 -->
    <div class="game-jury">
      <div class="title-box">
        <tit-comp tit="大赛评委"></tit-comp>
        <div class="arrow-box" @click="toList('jurys')">
          <img src="../../../assets/img/arrows.png" class="arrow">
        </div>
      </div>
      <div class="jury-box">
        <list-y v-for="item in judges" :key="item.img" :onItem='item'></list-y>
        <!-- <div v-for="item in judges" :key="item.img" class="jury-item" @click="juryDescribe(item.id)">
          <div class="jury-img-box">
            <el-image :src="item.avatar" class="img" fit="cover"></el-image>
          </div>
          <div class="jury-info-box">
            <div class="name">{{item.name}}</div>
            <div class="summary">{{item.desc}}</div>
          </div>
        </div> -->
      </div>
    </div>
    <!-- rule -->
    <div class="rule">
      <div>
        <tit-comp tit="大赛规则"></tit-comp>
      </div>
      <div class="rule-text">
        本次大会分为报名及地区初选、地区复选、地区决选、全国决选四个阶段进行，共设少儿组、1-2年级组、3年级组、4-6年级组四个组别。
      </div>
      <div class="rule-btn">
        <span @click="toRule">更多</span>
      </div>
    </div>
  </div>
</template>
<script>
import listY from '../../../components/list/listY.vue'
import listX from '../../../components/list/listX.vue'
import titComp from '../../../components/titleComp/titleComp'
import { mapState } from 'vuex'
export default {
  data () {
    return {
      contentPart: `“中国日报社‘21世纪杯’小学生英语演讲大会”和“中国日报社‘21世纪杯’少儿英语大会”分别创办于2002年和2016年，
        是由中国日报社主办、二十一世纪英语教育传媒承办的，面向全国少年儿童举办的大型英语展示活动`,
      introduceContent: `“中国日报社‘21世纪杯’小学生英语演讲大会”和“中国日报社‘21世纪杯’少儿英语大会”分别创办于2002年和2016年，
        是由中国日报社主办、二十一世纪英语教育传媒承办的，面向全国少年儿童举办的大型英语展示活动，活动旨在发展素质教育，
        培养和提高中国少年儿童的英语综合运用能力和跨文化交际能力。大会将以绘本演绎和主题演讲为主要形式，培养青少年家国情怀和文化自信为宗旨，
        传播中华优秀文化，讲好中国故事，构建中国特色的话语体系，向世界展现真实、立体、全面的中国。`,
      contentPartState: false,
      cooperationUnitData: [
        {
          img:
            '//qpic.y.qq.com/music_cover/AhbCa0vazSRDjEJhYwthgoxuDAGq1YzVEn28glx4EdSehmJ9ePSyCg/300?n=1',
          name: '公司名字',
          unit: '协办单位'
        },
        {
          img:
            '//qpic.y.qq.com/music_cover/O9FbmkBn7CWyTWMfweTS5zC2kibRo0MrY1rd9icc2E5ZGAB39yT7mETw/300?n=1',
          name: '公司名字',
          unit: '协办单位'
        },
        {
          img:
            '//qpic.y.qq.com/music_cover/8eiaDBJ27yYicpMibYZmmEdNEPOTSqwhACUph5d8R15xqhfJqdjIgDmYw/300?n=1',
          name: '公司名字',
          unit: '协办单位'
        },
        {
          img:
            '//y.gtimg.cn/music/photo_new/T015R640x360M101003g7v0p0nUSad.jpg?max_age=2592000',
          name: '公司名字',
          unit: '协办单位'
        }
      ],
      gameJuryData: [
        {
          img:
            '//y.gtimg.cn/music/photo_new/T002R300x300M000001tw42e24BvP7.jpg?max_age=2592000',
          name: '评委姓名',
          summary:
            '这是评委简介这是评委简介这是评委简介这是评委简介这是评委简介这是评委简介这是a阿萨德撒打算时尚大师大所多'
        },
        {
          img:
            '//y.gtimg.cn/music/photo_new/T002R300x300M000004XXSDl0scaHT.jpg?max_age=2592000',
          name: '评委姓名',
          summary:
            '这是评委简介这是评委简介这是评委简介这是评委简介这是评委简介这是评委简介这是萨德撒打算时尚大师大所多'
        },
        {
          img:
            '//y.gtimg.cn/music/photo_new/T015R640x360M101004Hj3sC1KmhVn.jpg?max_age=2592000',
          name: '评委姓名',
          summary:
            '这是评委简介这是评委简介这是评委简介这是评委简介这是评委简介这是评委简介这是萨德撒打算时尚大师大所多'
        }
      ]
    }
  },
  computed: {
    ...mapState({
      units: state => state.admin.gameIntroduction.units,
      judges: state => state.admin.gameIntroduction.judges
    })
  },
  components: {
    titComp,
    listX,
    listY
  },
  methods: {
    toList (name) {
      this.$router.push({ path: '/option/list', query: { name } })
    },
    juryDescribe (id) {
      this.$router.push({ path: '/option/juryRe', query: { id } })
    },
    clickRxpand () {
      this.contentPartState = !this.contentPartState
    },
    toRule () {
      this.$router.push({ path: '/option/rule' })
    }
  }
}
</script>
<style lang="less">
.game-introduce {
  width: 100%;
  box-sizing: border-box;
  padding: 0 18px;
  margin-bottom: 100px;
  .introduce {
    margin-top: 16px;
    width: 100%;
    border-radius: 4px;
    box-shadow: 0px -1px 5px 0px rgba(196, 196, 196, 0.2),
      0px 2px 8px 0px rgba(226, 226, 226, 0.5);
    border: solid 1px #f1f1f1;
    padding: 14px 27px;
    box-sizing: border-box;
    .title {
      text-align: center;
      font-size: 16px;
      color: #333333;
      font-weight: bold;
      letter-spacing: 1px;
      margin-bottom: 15px;
      line-height: 1.4;
    }
    .content {
      font-size: 14px;
      color: #333333;
      letter-spacing: 1px;
      text-align: justify;
      line-height: 1.4;
      margin-bottom: 15px;
    }
    .content-start {
      text-overflow: ellipsis;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
    }
    .btn {
      display: flex;
      align-items: center;
      justify-content: center;
      span {
        padding: 4px 23px;
        font-size: 12px;
        color: #fe741d;
        border-radius: 12px;
        border: 1px solid #fe741d;
      }
    }
  }
  .cooperation-units {
    margin-top: 16px;
    .title-box{
      display: flex;
      align-items: center;
      justify-content: space-between;
      .arrow-box{
        width: 40px;
        height: 25px;
        display: flex;
        align-items: center;
        justify-content: flex-end;
      }
      .arrow{
        width: 22px;
        height: 22px;
      }
    }
    .units-box {
      // margin-top: 12px;
      width: 100%;
      display: flex;
      align-items: center;
      justify-content: space-between;
      flex-wrap: wrap;
    }
  }
  .game-jury {
    margin-top: 16px;
    .title-box{
      display: flex;
      align-items: center;
      justify-content: space-between;
      .arrow-box{
        width: 40px;
        height: 25px;
        display: flex;
        align-items: center;
        justify-content: flex-end;
      }
      .arrow{
        width: 22px;
        height: 22px;
      }
    }
    .jury-box {

    }
  }
  .rule {
    margin-top: 20px;
    .rule-text {
      margin-top: 15px;
      font-size: 14px;
      color: #333333;
      letter-spacing: 1px;
      text-align: justify;
      line-height: 1.4;
      margin-bottom: 10px;
      text-overflow: ellipsis;
      overflow: hidden;
      text-overflow: ellipsis;
      display: -webkit-box;
      -webkit-line-clamp: 4;
      -webkit-box-orient: vertical;
    }
    .rule-btn {
      display: flex;
      align-items: center;
      justify-content: center;
      span {
        padding: 4px 23px;
        font-size: 12px;
        color: #fe741d;
        border-radius: 12px;
        border: 1px solid #fe741d;
      }
    }
  }
}
</style>



